import { CButton, CAlert } from "@coreui/react";
import axios from "axios";
import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import { selectUser } from "src/localyStore";

const Setting = () => {
  const [oldBanners, setOldBanners] = useState("");
  const [homeCategories, setHomeCategories] = useState("");
  const { token } = useSelector(selectUser);
  const [leftColor, setLeftColor] = useState("");
  const [rightColor, setRightColor] = useState("");
  const [productId, setProductId] = useState(""); //for category and product id
  const [bannerType, setBannerType] = useState("product");
  const [webBanner, setWebBanner] = useState(null);
  const [mobileBanner, setMobileBanner] = useState(null);
  const [categoryType, setCategoryType] = useState("list");

  const [reload, setReload] = useState(true);
  const [category, setCategory] = useState(null);
  const server = process.env.REACT_APP_SERVER;

  // alert
  const [event, setEvent] = useState(null);

  const clearEvent = (ms) => {
    setTimeout(() => {
      setEvent(null);
    }, ms);
  };

  //for homecategories
  const getCategoriesUrl =
    server + process.env.REACT_APP_ADMIN_All_CATEGORY_GET;
  const getHomeCategoriesUrl =
    server + process.env.REACT_APP_ADMIN_HOME_CATEGORY_GET;
  const postHomecategoriesUrl =
    server + process.env.REACT_APP_ADMIN_HOME_CATEGORY_POST;

  //for banner
  const getBannerUrl = server + process.env.REACT_APP_ADMIN_HOME_BANNER_GET;
  const postBannerUrl = server + process.env.REACT_APP_ADMIN_HOME_BANNER_POST;
  const deleteBannerUrl =
    server + process.env.REACT_APP_ADMIN_HOME_BANNER_DELETE;

  const getHomeBanner = () => {
    axios
      .get(getBannerUrl, {
        headers: {
          Authorization: "Bearer " + token,
        },
      })
      .then((response) => {
        setOldBanners(response.data.homepageBanner);
      })
      .catch((err) => {
        setOldBanners(null);
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
      });
  };
  const homeScreenBannerFormSubmit = (e) => {
    e.preventDefault();
    const PostBannerData = new FormData();
    PostBannerData.append("leftColor", leftColor);
    PostBannerData.append("rightColor", rightColor);
    PostBannerData.append("bannerMob", mobileBanner);
    PostBannerData.append("bannerWeb", webBanner);
    PostBannerData.append("bannerType", bannerType);
    // check type and append
    if (bannerType === "product") {
      PostBannerData.append("product", productId);
    } else {
      PostBannerData.append("categoryType", categoryType);
      PostBannerData.append("category", productId);
    }

    axios
      .post(postBannerUrl, PostBannerData, {
        headers: {
          Authorization: "Bearer " + token,
        },
      })
      .then((res) => {
        setReload(!reload);
        setLeftColor("");
        setRightColor("");
        setMobileBanner("");
        setWebBanner("");
        setCategoryType("");
        setProductId("");
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
        setReload(!reload);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
      });
  };
  const deleteHomeBanner = (id) => {
    axios
      .post(
        process.env.REACT_APP_SERVER + "/api/image-handler",
        {
          _id: id,
          name: "home-page-banner",
        },
        {
          headers: {
            Authorization: "Bearer " + token,
          },
        }
      )
      .then((res) => {
        setReload(!reload);
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setReload(!reload);
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
      });
  };

  const getAllCategories = () => {
    axios
      .get(getCategoriesUrl, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((response) => {
        setCategory(response.data.categories);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
        setReload(!reload);
      });
  };
  // get home categories
  const getHomeCategories = () => {
    axios
      .get(getHomeCategoriesUrl, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })
      .then((response) => {
        setHomeCategories(response.data.homeScreenCategories[0]);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err?.response?.data.message ?? "server-error",
        });
        clearEvent(5000);
      });
  };
  // create home categories
  const homeScreenCategoryFormSubmit = (e) => {
    e.preventDefault();
    let Category = [];
    for (let category = 0; category < e.target.category.length; category++) {
      if (e.target.category[category].checked) {
        Category.push(e.target.category[category].value);
      }
    }
    axios
      .post(
        postHomecategoriesUrl,
        {
          category: Category,
          sort: e.target.sort.value,
          limit: e.target.itemcount.value,
        },
        {
          headers: {
            Authorization: "Bearer " + token,
          },
        }
      )
      .then((res) => {
        setReload(!reload);
        setEvent({
          type: "success",
          massage: res.data.message,
        });
        clearEvent(5000);
      })
      .catch((err) => {
        setEvent({
          type: "error",
          massage: err.response.data.message,
        });
        clearEvent(5000);
      });
  };
  // init first load
  useEffect(() => {
    if (token) {
      getHomeBanner();
      getAllCategories();
      getHomeCategories();
    }
  }, [token, reload]);
  return (
    <div>
      <h2>HomePege Settings</h2>
      <hr />
      <br />
      <h4>Banner for Home Page</h4>
      <hr />
      <form
        style={{
          position: "relative",
        }}
        onSubmit={homeScreenBannerFormSubmit}
      >
        <div>
          <div className="d-flex align-items-center my-3">
            <p className="mt-3">BannerType:</p>
            <select
              required
              className="ml-4"
              style={{ height: "30px", width: "150px" }}
              onChange={(e) => setBannerType(e.target.value)}
            >
              <option value="product">Products</option>
              <option value="category">Category</option>
            </select>
          </div>
          {/* if banner type is category */}
          {bannerType === "category" && (
            <div className="d-flex align-items-center my-3">
              <p className="mt-3">Category type:</p>
              <select
                className="ml-4"
                style={{ height: "30px", width: "150px" }}
                onChange={(e) => setCategoryType(e.target.value)}
              >
                <option value="list">List</option>
                <option value="store">Store</option>
                <option value="brand">Brand</option>
              </select>
            </div>
          )}
          <div>
            <div className="my-2 d-flex align-items-center">
              <p className="mt-3 mr-4">{bannerType} Id.:</p>
              <input
                required
                type="text"
                placeholder="Enter valid Id"
                value={productId}
                onChange={(e) => setProductId(e.target.value)}
              />
            </div>
          </div>
        </div>
        <div className="d-flex align-items-center">
          <p className="mt-3 mr-4">Banner Color:</p>
          <div className="d-flex align-items-center">
            <input
              required
              type="color"
              value={leftColor}
              onChange={(e) => setLeftColor(e.target.value)}
            />
            <input
              required
              type="text"
              placeholder="Leftcolor"
              onChange={(e) => setLeftColor(e.target.value)}
              value={leftColor}
            />
          </div>
          <div className="d-flex align-items-center ml-4">
            <input
              required
              type="color"
              placeholder="Rightcolor"
              value={rightColor}
              onChange={(e) => setRightColor(e.target.value)}
            />
            <input
              required
              type="text"
              placeholder="Rightcolor"
              onChange={(e) => setRightColor(e.target.value)}
              value={rightColor}
            />
          </div>
        </div>
        <br />
        <div className="d-flex align-items-center mb-2">
          <p className="mt-3 mr-4">Web Banner:</p>
          <input
            required
            type="file"
            accept="image/png,image/jpeg,image/jpg,image/webp"
            placeholder="banner"
            onChange={(e) => setWebBanner(e.target.files[0])}
            value={webBanner?.fileName}
          />
        </div>
        *Note : Web banner size must be 1620 * 800 and left and right color must
        be provided \
        <div className="d-flex align-items-center mb-2">
          <p className="mt-3 mr-4">Mobile Banner:</p>
          <input
            required
            type="file"
            accept="image/png,image/jpeg,image/jpg,image/webp"
            placeholder="banner"
            onChange={(e) => setMobileBanner(e.target.files[0])}
            value={mobileBanner?.fileName}
          />
        </div>
        *Note : Mobile Banner size must be 380 * 173 and left and right color
        must be provided
        <CButton
          style={{
            position: "absolute",
            bottom: "0px",
            right: "10px",
            width: "200px",
          }}
          veriant="outline"
          color="success"
          type="submit"
        >
          Save
        </CButton>
      </form>
      <br />
      <p style={{ fontSize: "25px" }}>Banners List - </p>
      {oldBanners &&
        oldBanners?.map((item) => (
          <div
            key={item._id}
            style={{
              fontSize: "20px",
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
              margin: "20px",
            }}
          >
            <div>
              <img width="300px" src={item.bannerWeb?.[0].img} alt="banner" />
              <br />
              <img
                width="300px"
                className="mt-4"
                src={item.bannerMob?.[0].img}
                alt="banner"
              />
            </div>
            <div>
              Colors : <span>Left - {item.leftColor}</span> ;{" "}
              <span>Right - {item.rightColor}</span> ;{" "}
              <span>
                <button
                  onClick={() => deleteHomeBanner(item._id)}
                  style={{
                    outline: "none",
                    color: "white",
                    backgroundColor: "red",
                    display: "inline",
                    marginLeft: "50px",
                    padding: "4px 16px",
                  }}
                >
                  Delete
                </button>
              </span>
              <hr />
            </div>
          </div>
        ))}
      <hr />
      <h3>Choose Homepage Products Show </h3>
      <hr />
      <form
        style={{
          position: "relative",
        }}
        onSubmit={homeScreenCategoryFormSubmit}
      >
        <div style={{ display: "flex", justifyContent: "space-evenly" }}>
          <span>
            <label htmlFor="itemcount">
              Number Of Product Show in Homepage : -
            </label>
            <input
              required
              id="itemcount"
              name="itemcount"
              type="number"
              defaultValue={homeCategories?.limit}
            />
          </span>
          <span>
            <label htmlFor="sort">Choose Sort Type : -</label>
            <select name="sort" id="sort">
              <option value="time">Time</option>
              <option value="alfabet">Alfabet</option>
              <option value="rating">Rating</option>
              <option value="review">Review</option>
              <option value="price">Price</option>
              <option value="quantity">Quantity</option>
              <option value="-createdAt">createdAt</option>
              <option selected value={homeCategories?.sort}>
                {homeCategories?.sort}
              </option>
            </select>
          </span>
        </div>
        {category?.map((item, index) => {
          return (
            <div key={item._id}>
              {item?.children?.map((item, index1) => {
                if (item) {
                  return (
                    <div key={item._id}>
                      {index1 + 1} :
                      <input
                        key={item._id}
                        style={{ margin: "5px 10px" }}
                        type="checkbox"
                        defaultChecked={
                          homeCategories?.category?.indexOf(item._id) !== -1
                        }
                        name="category"
                        value={item._id}
                      />
                      {item.name}
                    </div>
                  );
                }
              })}
            </div>
          );
        })}
        <br />
        <br />
        <CButton
          style={{
            position: "absolute",
            bottom: "0px",
            right: "10px",
            width: "200px",
          }}
          veriant="outline"
          color="success"
          type="submit"
        >
          Save
        </CButton>
      </form>
      {event?.type === "error" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="danger">{event.massage}</CAlert>
        </div>
      )}
      {event?.type === "success" && (
        <div className="position-fixed fixed-top d-flex justify-content-center">
          <CAlert color="success">{event.massage}</CAlert>
        </div>
      )}
    </div>
  );
};

export default Setting;
